import { useState } from 'react'
import { useTranslation } from 'react-i18next'
import { 
  Play, 
  Pause, 
  Volume2, 
  VolumeX, 
  Maximize, 
  ArrowLeft,
  ArrowRight,
  Home,
  Baby,
  Utensils,
  Gamepad2
} from 'lucide-react'
import { useLanguage } from '../../hooks/useLanguage'

const VirtualTourPage = () => {
  const { t } = useTranslation()
  const { isRTL } = useLanguage()
  const [currentRoom, setCurrentRoom] = useState(0)
  const [isPlaying, setIsPlaying] = useState(false)
  const [isMuted, setIsMuted] = useState(false)

  const rooms = [
    {
      id: 'accueil',
      name: isRTL ? ' ' : 'Zone d\'accueil',
      description: isRTL 
        ? '     '
        : 'Un espace chaleureux et accueillant pour recevoir enfants et parents',
      image: 'https://images.unsplash.com/photo-1559455800-c0c5c3d9b8c3?w=800',
      icon: Home,
      features: [
        isRTL ? ' ' : 'Bureau d\'accueil',
        isRTL ? '  ' : 'Zone d\'attente confortable',
        isRTL ? '  ' : 'Casiers personnels pour enfants'
      ]
    },
    {
      id: 'jeux',
      name: isRTL ? ' ' : 'Espace de jeux',
      description: isRTL 
        ? '     '
        : 'Un grand espace ddi aux jeux et activits ludiques',
      image: 'https://images.unsplash.com/photo-1544717297-fa95b6ee9643?w=800',
      icon: Gamepad2,
      features: [
        isRTL ? '  ' : 'Jeux ducatifs varis',
        isRTL ? '  ' : 'Zone de jeu scurise',
        isRTL ? ' ' : 'Activits motrices'
      ]
    },
    {
      id: 'repos',
      name: isRTL ? ' ' : 'Dortoir',
      description: isRTL 
        ? '    '
        : 'Un lieu calme et confortable pour le repos des enfants',
      image: 'https://images.unsplash.com/photo-1586023492125-27b2c045efd7?w=800',
      icon: Baby,
      features: [
        isRTL ? '  ' : 'Lits confortables et scuriss',
        isRTL ? ' ' : 'clairage tamis',
        isRTL ? ' ' : 'Ventilation naturelle'
      ]
    },
    {
      id: 'repas',
      name: isRTL ? ' ' : 'Salle de repas',
      description: isRTL 
        ? '    '
        : 'Un espace propre et hyginique pour les repas',
      image: 'https://images.unsplash.com/photo-1571019613454-1cb2f99b2d8b?w=800',
      icon: Utensils,
      features: [
        isRTL ? '   ' : 'Tables et chaises adaptes aux enfants',
        isRTL ? '  ' : 'Cuisine entirement quipe',
        isRTL ? '  ' : 'Standards d\'hygine levs'
      ]
    }
  ]

  const nextRoom = () => {
    setCurrentRoom((prev) => (prev + 1) % rooms.length)
  }

  const prevRoom = () => {
    setCurrentRoom((prev) => (prev - 1 + rooms.length) % rooms.length)
  }

  const currentRoomData = rooms[currentRoom]
  const CurrentIcon = currentRoomData.icon

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <div className="bg-white shadow-sm border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-3xl font-bold text-gray-900">
                {isRTL ? ' ' : 'Visite Virtuelle'}
              </h1>
              <p className="text-gray-600 mt-2">
                {isRTL 
                  ? '    '
                  : 'Dcouvrez nos installations depuis chez vous'
                }
              </p>
            </div>
            
            {/* Navigation des salles */}
            <div className="flex items-center space-x-4 rtl:space-x-reverse">
              <button
                onClick={prevRoom}
                className="p-2 rounded-full bg-gray-100 hover:bg-gray-200 transition-colors"
              >
                <ArrowLeft className={`w-5 h-5 ${isRTL ? 'rotate-180' : ''}`} />
              </button>
              
              <div className="text-center">
                <div className="text-sm text-gray-500">
                  {currentRoom + 1} / {rooms.length}
                </div>
              </div>
              
              <button
                onClick={nextRoom}
                className="p-2 rounded-full bg-gray-100 hover:bg-gray-200 transition-colors"
              >
                <ArrowRight className={`w-5 h-5 ${isRTL ? 'rotate-180' : ''}`} />
              </button>
            </div>
          </div>
        </div>
      </div>

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Visite principale */}
          <div className="lg:col-span-2">
            <div className="bg-white rounded-2xl shadow-lg overflow-hidden">
              {/* Image/Vido principale */}
              <div className="relative aspect-video bg-gray-900">
                <img
                  src={currentRoomData.image}
                  alt={currentRoomData.name}
                  className="w-full h-full object-cover"
                />
                
                {/* Contrles de lecture */}
                <div className="absolute inset-0 bg-black bg-opacity-30 flex items-center justify-center opacity-0 hover:opacity-100 transition-opacity">
                  <div className="flex items-center space-x-4 rtl:space-x-reverse">
                    <button
                      onClick={() => setIsPlaying(!isPlaying)}
                      className="p-4 bg-white bg-opacity-20 backdrop-blur-sm rounded-full text-white hover:bg-opacity-30 transition-all"
                    >
                      {isPlaying ? (
                        <Pause className="w-8 h-8" />
                      ) : (
                        <Play className="w-8 h-8" />
                      )}
                    </button>
                    
                    <button
                      onClick={() => setIsMuted(!isMuted)}
                      className="p-3 bg-white bg-opacity-20 backdrop-blur-sm rounded-full text-white hover:bg-opacity-30 transition-all"
                    >
                      {isMuted ? (
                        <VolumeX className="w-6 h-6" />
                      ) : (
                        <Volume2 className="w-6 h-6" />
                      )}
                    </button>
                    
                    <button className="p-3 bg-white bg-opacity-20 backdrop-blur-sm rounded-full text-white hover:bg-opacity-30 transition-all">
                      <Maximize className="w-6 h-6" />
                    </button>
                  </div>
                </div>

                {/* Indicateur de salle */}
                <div className="absolute top-4 left-4 bg-white bg-opacity-90 backdrop-blur-sm rounded-lg px-4 py-2">
                  <div className="flex items-center space-x-2 rtl:space-x-reverse">
                    <CurrentIcon className="w-5 h-5 text-primary-600" />
                    <span className="font-semibold text-gray-900">
                      {currentRoomData.name}
                    </span>
                  </div>
                </div>
              </div>

              {/* Description */}
              <div className="p-6">
                <h2 className="text-2xl font-bold text-gray-900 mb-3">
                  {currentRoomData.name}
                </h2>
                <p className="text-gray-600 mb-6">
                  {currentRoomData.description}
                </p>

                {/* Caractristiques */}
                <div>
                  <h3 className="text-lg font-semibold text-gray-900 mb-3">
                    {isRTL ? '' : 'Caractristiques'}
                  </h3>
                  <ul className="space-y-2">
                    {currentRoomData.features.map((feature, index) => (
                      <li key={index} className="flex items-center text-gray-600">
                        <div className="w-2 h-2 bg-primary-600 rounded-full mr-3 rtl:ml-3 rtl:mr-0"></div>
                        {feature}
                      </li>
                    ))}
                  </ul>
                </div>
              </div>
            </div>
          </div>

          {/* Sidebar */}
          <div className="space-y-6">
            {/* Navigation des salles */}
            <div className="bg-white rounded-2xl shadow-lg p-6">
              <h3 className="text-lg font-semibold text-gray-900 mb-4">
                {isRTL ? ' ' : 'Explorer les espaces'}
              </h3>
              
              <div className="space-y-3">
                {rooms.map((room, index) => {
                  const RoomIcon = room.icon
                  return (
                    <button
                      key={room.id}
                      onClick={() => setCurrentRoom(index)}
                      className={`w-full flex items-center p-3 rounded-lg text-left rtl:text-right transition-all ${
                        currentRoom === index
                          ? 'bg-primary-100 text-primary-700 border-primary-200'
                          : 'hover:bg-gray-50 text-gray-600'
                      }`}
                    >
                      <RoomIcon className="w-5 h-5 mr-3 rtl:ml-3 rtl:mr-0 flex-shrink-0" />
                      <div>
                        <div className="font-medium">{room.name}</div>
                        <div className="text-sm opacity-75 truncate">
                          {room.description}
                        </div>
                      </div>
                    </button>
                  )
                })}
              </div>
            </div>

            {/* Informations pratiques */}
            <div className="bg-white rounded-2xl shadow-lg p-6">
              <h3 className="text-lg font-semibold text-gray-900 mb-4">
                {isRTL ? ' ' : 'Informations pratiques'}
              </h3>
              
              <div className="space-y-4">
                <div>
                  <div className="font-medium text-gray-900">
                    {isRTL ? ' ' : 'Horaires'}
                  </div>
                  <div className="text-gray-600 text-sm">
                    {isRTL ? ' - : 7:00 - 18:00' : 'Lundi - Vendredi: 7h00 - 18h00'}
                  </div>
                </div>
                
                <div>
                  <div className="font-medium text-gray-900">
                    {isRTL ? '' : 'Capacit'}
                  </div>
                  <div className="text-gray-600 text-sm">
                    {isRTL ? '50   ' : '50 enfants maximum'}
                  </div>
                </div>
                
                <div>
                  <div className="font-medium text-gray-900">
                    {isRTL ? ' ' : 'ge'}
                  </div>
                  <div className="text-gray-600 text-sm">
                    {isRTL ? '3  - 6 ' : '3 mois - 6 ans'}
                  </div>
                </div>
              </div>

              <div className="mt-6 pt-6 border-t border-gray-200">
                <button className="w-full btn-primary">
                  {isRTL ? ' ' : 'Rserver une visite'}
                </button>
              </div>
            </div>

            {/* Contact rapide */}
            <div className="bg-gradient-to-br from-primary-500 to-primary-600 rounded-2xl shadow-lg p-6 text-white">
              <h3 className="text-lg font-semibold mb-3">
                {isRTL ? '  ' : 'Des questions ?'}
              </h3>
              <p className="text-primary-100 text-sm mb-4">
                {isRTL 
                  ? '     '
                  : 'Notre quipe est disponible pour rpondre  toutes vos questions'
                }
              </p>
              <button className="w-full bg-white text-primary-600 font-semibold py-2 px-4 rounded-lg hover:bg-gray-50 transition-colors">
                {isRTL ? ' ' : 'Nous contacter'}
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default VirtualTourPage
