/**
 * Utilitaires pour la gestion des devises
 */

/**
 * Formate un montant en dinar tunisien
 * @param {number} amount - Le montant  formater
 * @param {string} locale - La locale (par dfaut 'fr-TN')
 * @returns {string} Le montant format
 */
export const formatTND = (amount, locale = 'fr-TN') => {
  if (typeof amount !== 'number' || isNaN(amount)) {
    return '0 TND'
  }

  try {
    // Utiliser Intl.NumberFormat pour un formatage correct
    const formatter = new Intl.NumberFormat(locale, {
      style: 'currency',
      currency: 'TND',
      minimumFractionDigits: 0,
      maximumFractionDigits: 3
    })
    
    return formatter.format(amount)
  } catch (error) {
    // Fallback si la locale n'est pas supporte
    return `${amount.toLocaleString()} TND`
  }
}

/**
 * Formate un montant simple avec TND
 * @param {number} amount - Le montant  formater
 * @returns {string} Le montant format
 */
export const formatSimpleTND = (amount) => {
  if (typeof amount !== 'number' || isNaN(amount)) {
    return '0 TND'
  }
  
  return `${amount.toLocaleString()} TND`
}

/**
 * Parse un montant depuis une chane
 * @param {string} amountString - La chane contenant le montant
 * @returns {number} Le montant pars
 */
export const parseAmount = (amountString) => {
  if (typeof amountString !== 'string') {
    return 0
  }
  
  // Supprimer les espaces, TND, et autres caractres non numriques sauf . et ,
  const cleanString = amountString.replace(/[^\d.,]/g, '')
  
  // Remplacer la virgule par un point pour le parsing
  const normalizedString = cleanString.replace(',', '.')
  
  const parsed = parseFloat(normalizedString)
  return isNaN(parsed) ? 0 : parsed
}

/**
 * Constantes pour les devises
 */
export const CURRENCY = {
  TND: 'TND',
  SYMBOL: '.', // Symbole du dinar tunisien en arabe
  NAME_FR: 'Dinar Tunisien',
  NAME_AR: ' '
}

export default {
  formatTND,
  formatSimpleTND,
  parseAmount,
  CURRENCY
}
